          var dataInicial = new Date(util.addOrSubtractDays(dataHoje, -button.value));                    
          getApontamentosByDateRangeAndEquipe(dataInicial, {dias: button.value+1, lineInterval: button.value+1}, $scope.equipe.componentes, false, false, true);//atualiza s√≥ o lineData
