          $scope.allDataInicial = $filter('date')(dateAux, 'dd/MM/yyyy');
          $scope.allDataFinal = $filter('date')(endDateAux, 'dd/MM/yyyy');

          $scope.allApontamentos = objRetornoCalculo.arrayItemApontamentos;
